*::before, *::after {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}


body{
    font-family:  'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: inherit;
}



nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: #ffffff;
    color: yellow;
    padding: -1px;
    padding-left: 300px;
    padding-right: 150px;
    margin-top: 40px;
}

.gitlink{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fbcfe8;
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    padding-left: 30px;
    padding-right: 10px;
    font-size: 15px;
}

.gitlink a{
    color: #9d174d;
}

.logo a{
    color: green;
    text-decoration: none ;
}

.nav-links{
    display: flex;
}

.logo{
    list-style: none;
    display: flex;
}
  
.nav-item a{
    display: inline-block;
    padding: 20px 15px;
    text-decoration: none;
    color: green;
}
  
.nav-item:hover {
    background-color: hotpink;
}
  
.nav-item:hover a {
    color: darkgreen;
}

li{
    list-style: none;
}

.hero{
    background-color: #dce9fe;
    padding: 300px;
    padding-left: 200px;
    font-family: initial;
   
}
h2{
    font-family: initial;
}

.container{
    background-color: #fff;
    padding: 300px;
    padding-left: 190px;
    word-spacing: 2px;
}




